.container {
  max-width: 800px;
  margin: 40px auto;
  text-align: center;
  font-family: Arial, sans-serif;
  background: #f8f9fa;
  padding: 20px;
  border-radius: 10px;
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.1);
  transition: background 0.3s, color 0.3s;
  min-height: 90vh;
}

.container.dark {
  background: #1e1e1e;
  color: white;
}

.header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 20px;
}

.dark-mode-toggle {
  background: transparent;
  border: none;
  cursor: pointer;
  font-size: 24px;
  color: #333;
  transition: color 0.3s;
}

.container.dark .dark-mode-toggle {
  color: white;
}

.dark-mode-toggle:hover {
  color: #555;
}